import React, { useState } from 'react';
import '../../App.css';
import './About.css';
import CustomFooter from '../footer/Footer';

function About() {

    const [showFoodInfo, setShowFoodInfo] = useState(false);
    const [showFootballInfo, setShowFootballInfo] = useState(false);
    const [showFamilyInfo, setShowFamilyInfo] = useState(false);
    const [showFreedomInfo, setShowFreedomInfo] = useState(false);

    function handleClick(value) {
        switch(value) {
            case "FOOD":
                setShowFoodInfo(!showFoodInfo);
                setShowFootballInfo(false);
                setShowFamilyInfo(false);
                setShowFreedomInfo(false);
                break;
            case "FOOTBALL":
                setShowFoodInfo(false);
                setShowFootballInfo(!showFootballInfo);
                setShowFamilyInfo(false);
                setShowFreedomInfo(false);
                break;
            case "FAMILY":
                setShowFoodInfo(false);
                setShowFootballInfo(false);
                setShowFamilyInfo(!showFamilyInfo);
                setShowFreedomInfo(false);
                break;
            case "FREEDOM":
                setShowFoodInfo(false);
                setShowFootballInfo(false);
                setShowFamilyInfo(false);
                setShowFreedomInfo(!showFreedomInfo);
                break;
            default:
                break;
        };
    }

    function infoPane() {
        if (showFoodInfo) {
            return (
                <div class="info-pane">
                    <div class="text-container-left">
                        Food is a quintessential piece of any Thanksgiving. Without it, Thanksgiving as a holiday would be lost. 
                        Now imagine eating that food with sweeping views of the pond, surrounded by nature.
                        Cap the night off with some pumpkin pie and roasted marshmallows.
                        Does it get any better than that?
                    </div>
                    <img className="img-right" src="./images/dinner.jpg" alt="delcicious food" />
                </div>
            );
        } else if (showFootballInfo) {
            return (
                <div class="info-pane">
                    <div class="text-container-right">
                        Football: America's pastime. Can you imagine a thanksgiving without it?
                        Thanksgiving in the suburbs lacks the outdoor space to truly experience America's sport.
                        Punky Hollow's spacious confines allows for the perfect game of 2-hand-touch.
                    </div>
                    <img className="img-left" src="./images/football.jpg" alt="football" />
                </div>
            );
        } else if (showFamilyInfo) {
            return (
                <div class="info-pane">
                    <div class="text-container-left">
                        Family is what Thanksgiving is all about right? Well, except when you get tired of it.
                        Luckily, with its sweeping estate, Punky Hollow has plenty of nooks and crannies to escape to when Family Thanksgiving becomes too much.
                    </div>
                    <img className="img-right" src="./images/leader.jpg" alt="fearless leader" />
                </div>
            );
        } else if (showFreedomInfo) {
            return (
                <div class="info-pane">
                    <div class="text-container-right">
                        Nothing is more patriotic than Thanksgiving in Rural America.
                        Take Thanksgiving back to its roots: Punky Hollow.
                        <br />
                        <br />
                        <b>Make Thanksgiving Great Again</b>
                    </div>
                    <img className="img-right" src="./images/punky5.jpg" alt="punky" />
                </div>
            );
        } else {
            return null;
        }
    }

    return(
        <div className="App">
            <header>
                <img src="./images/punky-2020-banner.png" className="banner-img" alt="PUNKY HOLLOW 2020"/>
            </header>
            <form className="link-btn-container-3" action="/home">
                <input type="submit" value="RETURN HOME" className="link-btn" />
            </form>
            <img className="background-img" src="./images/punky1.jpg" alt="BEAUTIFUL PUNKY HOLLOW"/>
            <div className="about-info-header">
                <h1 className="about-h1">Why Punky Hollow?</h1>
            </div>
            <div className="about-info-container">
                <div className="about-info-grid">
                    <div className="about-info-btn-container">
                        <button className="about-info-btn" onClick={() => handleClick("FOOD")}>FOOD</button>
                    </div>
                    <div className="about-info-btn-container">
                        <button className="about-info-btn" onClick={() => handleClick("FOOTBALL")}>FOOTBALL</button>
                    </div>
                    <div className="about-info-btn-container">
                        <button className="about-info-btn" onClick={() => handleClick("FAMILY")}>FAMILY</button>
                    </div>
                    <div className="about-info-btn-container">
                        <button className="about-info-btn" onClick={() => handleClick("FREEDOM")}>FREEDOM</button>
                    </div>
                </div>
                { infoPane() }
            </div>
            <CustomFooter />
        </div>
    );
}

export default About;